<template>
  <div class="m-product-categroy">
    <dl class="classic">
      <dt></dt>
      <dt></dt>
      <dd v-for="(item) in classicList" :key="item.type">
        <m-select :name="item.title" :list="item.subList"/>
      </dd>
    </dl>
    <dl class="classic">
      <dt></dt>
      <dt></dt>
      <dd v-for="(item) in areaList" :key="item.type">
        <m-select :name="item.title" :list="item.sub_list"/>
      </dd>
    </dl>
  </div>
</template>
<script>
import MSelect from "./select.vue";
import api from "@/api/index.js"
export default {
  data() {
    return {
      classicList: [],
      areaList: [
        {
          title: "",
          sub_list: [
            {
              name: "",

              id: 120000
            },

            {
              name: "",

              id: 12222
            }
          ]
        }
      ]
    };
  },
  created(){
    //
    api.classify().then( res => {
      console.log('dfsdfd',res);
      this.classicList = res.data.data;
    })
    //
    api.areaList().then( res => {
      console.log('sssss',res);
      // var json = JSON.parse(res.data.data)
      // console.log(json);
      // //this.areaList = res.data.data
    })
  },
  components: {
    MSelect
  }
};
</script>




